<template>
  <div class="button-wrapper">
    <base-button type="secondary" class="text-capitalize text-lg" @click="tryAgain">Try again</base-button>
  </div>
</template>

<script>
import {
  CURRENT_EXERCISE,
  CURRENT_EXERCISE_TYPE,
  RESET_ANSWER_HISTORY
} from '@/mutation-types'

import { mapMutations } from 'vuex'

import ExerciseTypes from '@/helpers/ExerciseTypes'

export default {
  name: 'TryAgain',
  methods: {
    tryAgain() {
      this.setCurrentExercise(0)
      this.setCurrentExerciseType(ExerciseTypes.getType(0))
      this.resetAnswerHistory()

      this.$emit('try-again')
    },
    ...mapMutations('exercises',{
      setCurrentExercise: CURRENT_EXERCISE,
      setCurrentExerciseType: CURRENT_EXERCISE_TYPE,
      resetAnswerHistory: RESET_ANSWER_HISTORY
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/sass/exercises/_button.scss";
</style>
