<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div v-if="courseData.course_title" class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Dashboard</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">{{ courseData.course_title }}</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item"><router-link to="/home">Dashboard</router-link></li>
              <li class="breadcrumb-item"><router-link to="/courses">Courses</router-link></li>
              <li class="breadcrumb-item active" aria-current="page">{{ courseData.course_title }}</li>
            </ol>
          </nav>
        </div>
        <div v-if="courseData.course_title" class="buttons col-2 col-md-3 col-lg-2">
          <div class="row">
            <div class="">
              <base-button class="btn-icon btn-block" type="secondary" :class="[enrolled ? 'text-danger' : '']" @click="enrollInCourse">
                <i class="fas m-0" :class="[enrolled ? 'fa-minus' : 'fa-plus']"></i>
                <span class="d-none d-md-block">&nbsp;Enroll<span v-show="enrolled">ed</span></span>
              </base-button>
            </div>
          </div>
        </div>
      </div>
    </base-header>
    <!--    COURSE CONTENT-->
    <div class="container-fluid mt-md--6">
      <div class="row">
        <div class="col-12">
          <div class="row">
            <div class="col-sm-8 offset-sm-2">
              <div v-if="!courseData.course_title" class="col-12 d-flex align-items-center pt-4 pt-sm-0 lesson-loading">
                <i class="fas fa-spinner fa-spin mx-auto"></i>
              </div>
            </div>
          </div>
          <transition name="fade">
            <div v-if="courseData.course_title" >
              <div class="row pb-2">
                <div class="col-6 offset-3 col-lg-3 offset-lg-0 pb-3 pb-lg-0">
                  <transition name="fade">
                    <div v-lazy-container="{ selector: 'img' }">
                      <img
                        class="card-img"
                        :data-src="`https://chinesepod.com/images/courses/${courseData.id}/course.svg`"
                        :data-loading="preloaderSquare"
                        :data-error="preloaderSquare"
                        :alt="courseData.course_title">
                    </div>
                  </transition>
                </div>
                <div class="col-12 col-lg-9">
                  <stats-card class="bg-gradient-blue">
                    <div class="row">
                      <div class="col">
                        <h5 class="card-title text-uppercase text-muted mb-0 text-white">In this Course
                          <!--                                      <badge size="lg" class="float-right text-white" :class="lessonLevel">{{ courseData.level }}</badge>-->
                        </h5>
                        <!--                      <p class="card-body mb-0 text-white">{{ courseData.course_introduction.trim() }}</p>-->
                        <p class="card-body mb-0 text-white" v-html="courseData.course_introduction"></p>
                      </div>
                    </div>
                    <template slot="footer">

                    </template>
                  </stats-card>
                </div>
              </div>
              <div class="row">
                <div v-for="lesson in courseData.lessons" :key="lesson.id" class="col-6 col-md-4 col-lg-3">
                  <lesson-card :lesson="lesson.lesson" :courseId="courseData.id"/>
                </div>
              </div>
            </div>
          </transition>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import preloaderSquare from '@/assets/img/preloader-square.svg'
  import {mapState} from 'vuex'
  import LessonCard from "../../components/Dashboard/LessonCard";

  export default {
    name: "CourseDetails",
    components: {LessonCard},
    title () {`${this.courseData && this.courseData.course_title ? this.courseData.course_title : 'Lesson Courses'} | ChinesePod`},
    data () {
      return {
        courseData: {},
        preloaderSquare: preloaderSquare,
        enrolled: false
      }
    },
    computed: {
      ...mapState({
        courses: 'courses'
      })
    },
    methods: {
      async getCourse () {
        this.courseData = await this.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/get-course?courseId=${this.$route.params.courseId}`)
          .then(({data}) => {return data})
          .catch(() => {window.location.href = '/home'});
      },
      async enrollInCourse () {
        this.enrolled = !this.enrolled;
        await this.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-course', {
          courseId: this.courseData.id,
          status: this.enrolled
        }).then(() => {
          this.$notify({type: this.enrolled ? 'success' : 'warning', message: `${this.enrolled? 'Enrolled in' : 'Dropped'} course: ${ this.courseData.course_title }`});
        })
          .catch(() => {});
      }
    },
    async beforeMount () {
      await this.getCourse();
      this.enrolled =  this.courses.filter((item) => item.course.id === this.courseData.id).length > 0
    }
  }
</script>

