<script>
  import convertPinyinTones from 'pinyin-tone-converter';

  import {Editor, EditorContent, EditorMenuBar, EditorFloatingMenu} from 'tiptap'
  import {
    Blockquote,
    CodeBlock,
    HardBreak,
    Heading,
    HorizontalRule,
    OrderedList,
    BulletList,
    ListItem,
    TodoItem,
    TodoList,
    Bold,
    Code,
    Italic,
    Link,
    Strike,
    Underline,
    History,
    Placeholder,
    Image
  } from 'tiptap-extensions'

  export default {
    name: "Editor",
    components: {
      // Editor,
      // Icon,
      // EditorFloatingMenu,
      EditorMenuBar,
      // Comment,
      EditorContent
    },
    props: {
      replyTo: String
    },
    beforeDestroy() {
      this.editor.destroy()
    },
    data () {
      return {
        editor: new Editor({
          extensions: [
            new Placeholder({
              emptyNodeClass: 'is-empty',
              emptyNodeText: 'Leave a comment …'
            }),
            new Blockquote(),
            new BulletList(),
            new CodeBlock(),
            new HardBreak(),
            new Heading({ levels: [1, 2, 3] }),
            new HorizontalRule(),
            new ListItem(),
            new OrderedList(),
            new TodoItem(),
            new TodoList(),
            new Link(),
            new Bold(),
            new Code(),
            new Italic(),
            new Strike(),
            new Underline(),
            new History(),
            new Image()
          ]
        }),
        linkUrl: null,
        linkMenuIsActive: false
      }
    },
    methods: {
      setContent(content, index = 0) {
        this.$refs.comment.$el.children[0].focus();
        this.editor.setContent(content);
      },
      showImagePrompt(command) {
        const src = prompt('Copy the url of your image here');
        if (src !== null) {
          command({ src })
        }
      },
      clearContent() {this.editor.clearContent()},
      getHTML() {return this.editor.getHTML()},
      // showLinkMenu(attrs) {
      //   this.linkUrl = attrs.href;
      //   this.linkMenuIsActive = true;
      //   this.$nextTick(() => {
      //     this.$refs.linkInput.focus()
      //   })
      // },
      // hideLinkMenu() {
      //   this.linkUrl = null;
      //   this.linkMenuIsActive = false
      // },
      // setLinkUrl(command, url) {
      //   command({ href: url });
      //   this.hideLinkMenu()
      // },
      convertToPinyin() {
        let html = this.editor.getHTML();
        this.editor.setContent(convertPinyinTones(this.editor.getHTML()))
      }
    }
  }
</script>

