<template>
  <div class="exercise-block">
    <exercise-header
      title="Match the phrase with the translation"
      :index="index"
      :is-answer="isAnswer"
      :total-score="totalScore"
      :exercise-score="exercise.score"
      :user-score="userScore"
      remove-padding-from-score="remove-padding-from-score"></exercise-header>
    <div class="parts" :id="'parts-' + exercise.id">
      <div class="parts-left col-6">
        <base-button
          v-for="(part, index) in leftParts"
          :type="isAnswer ? colors[part.id - 1] : buttonColors.left[index] ? buttonColors.left[index] : 'light'"
          :key="part['s']"
          :ref="'left-' + exercise.id + '-' + part.id"
          :data-tag="part.id"
          :class="[{
            'match-answers': isAnswer,
            'border border-success': isCorrect('right', part.id),
            'border border-danger': isWrong('right', part.id)
          }, 'btn-block', 'mb-2']" @click="mark($event, 'left', exercise.id, part.id, index)"
          :disabled="isAnswer">
          <div class="row">
            <div class="col-12 characters">{{ part[charSet.slice(0,1)] }}</div>
            <div class="col-12 pinyin text-white" v-if="charSet !== 'pinyin' && pinyin">{{ part['p'] }}</div>
          </div>
        </base-button>
      </div>
      <div class="parts-right col-6">
<!--        :type="isAnswer ? colors[part.id - 1] : 'light'"-->
        <base-button
          v-for="(part, index) in rightParts"
          :type="isAnswer ? colors[part.id - 1] : buttonColors.right[index] ? buttonColors.right[index] : 'light'"
          :key="part['e']"
          :ref="'right-' + exercise.id + '-' + part.id"
          :data-tag="part.id"
          :class="[{
            'match-answers': isAnswer,
            'border border-success': isCorrect('right', part.id),
            'border border-danger': isWrong('right', part.id)
          }, 'btn-block', 'mb-2']" @click="mark($event, 'right', exercise.id, part.id, index)"
          :disabled="isAnswer">
          <div class="row">
            <div class="col-12 english text-white">{{ part['e'] }}</div>
          </div>
        </base-button>
      </div>
    </div>
  </div>
</template>

<script>
  import shuffle from "lodash.shuffle"
  import { mapState } from 'vuex'
  import LanguageNumber from '@/mixins/LanguageNumber'
  import Exercise from '@/mixins/Exercise'
  import ExerciseHeader from '@/components/Exercises/ExerciseHeader'

  export default {
    name: "MatchingExercise",
    components: {
      ExerciseHeader
    },
    mixins: [LanguageNumber, Exercise],
    data() {
      return {
        left: null,
        right: null,
        leftSelected: false,
        leftSelectedTag: '',
        leftSelectedRef: '',
        rightSelected: false,
        rightSelectedTag: '',
        rightSelectedRef: '',
        currentColor: '',
        buttonColors: {
          left: ['light', 'light', 'light', 'light', 'light'],
          right: ['light', 'light', 'light', 'light', 'light']
        },
        answers: [],
        colors: ['primary', 'info', 'success', 'default', 'warning', 'danger']
      }
    },
    watch: {

    },
    computed: {
      leftParts() {
        if (this.left !== null) {
          return this.left;
        }
        return []
      },
      rightParts() {
        if (this.right !== null) {
          return this.right
        }
        return []
      },
      ...mapState({
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet
      })
    },
    mounted() {
      this.left = shuffle(this.exercise.question.segments);
      this.right = shuffle(this.exercise.question.segments);
    },
    methods: {
      color () {

        return 'light'
      },
      mark (event, side, exerciseId, id, index) {
        switch (side) {
          case 'left':
            if (this.answers.filter(({from}) => from === id).length > 0) {
              let cleanup = this.answers.filter(({from}) => from === id)[0];
              this.buttonColors['left'][cleanup.leftIndex] = 'light';
              this.buttonColors['right'][cleanup.rightIndex] = 'light';
              this.colors.push(cleanup.color);
              this.answers = this.answers.filter(({from}) => from !== id);
            }
            if (this.leftSelected) {
              this.buttonColors[side][this.leftSelectedRef] = 'light'
            }
            this.leftSelected = true;
            this.leftSelectedRef = index;
            this.leftSelectedTag = id;
            break;
          case 'right':
            if (this.answers.filter(({to}) => to === id).length > 0) {
              let cleanup = this.answers.filter(({to}) => to === id)[0];
              this.buttonColors['left'][cleanup.leftIndex] = 'light';
              this.buttonColors['right'][cleanup.rightIndex] = 'light';
              this.colors.push(cleanup.color);
              this.answers = this.answers.filter(({to}) => to !== id);
            }
            if (this.rightSelected) {
              this.buttonColors[side][this.rightSelectedRef] = 'light'
            }
            this.rightSelected = true;
            this.rightSelectedRef = index;
            this.rightSelectedTag = id;
            break;
        }
        if (!this.currentColor) {
          this.currentColor = this.colors[0]
        }
        this.buttonColors[side][index] = this.currentColor;
        this.$forceUpdate();
        if (this.leftSelected && this.rightSelected) {
          this.answers.push({color: this.colors.shift(),from: this.leftSelectedTag, to: this.rightSelectedTag, leftIndex: this.leftSelectedRef, rightIndex: this.rightSelectedRef});
          this.leftSelected = this.rightSelected = false;
          this.currentColor = this.leftSelectedRef = this.rightSelectedRef = this.leftSelectedTag = this.rightSelectedTag = '';
        }
        this.emitAnswer()
      },
      isCorrect(side, tag) {
        if (!this.isAnswer) {
          return false
        }

        const answers = this.answer.answer;

        if (side === 'left') {
          const answer = answers.filter(answer => answer.from === tag)[0]
          if (typeof answer !== 'undefined' && answer.to && answer.from) {
            return answer.from === answer.to
          } else {
            return false
          }
        } else {
          const answer = answers.filter(answer => answer.to === tag)[0]
          if (typeof answer !== 'undefined' && answer.to && answer.from) {
            return answer.from === answer.to
          } else {
            return false
          }
        }
      },
      isWrong(side, tag) {
        if (!this.isAnswer) {
          return false
        }

        const answers = this.answer.answer;

        if (side === 'left') {
          const answer = answers.filter(answer => answer.from === tag)[0]

          if (typeof answer !== 'undefined' && answer.to && answer.from) {
            return answer.from !== answer.to
          } else {
            return true
          }

        } else {
          const answer = answers.filter(answer => answer.to === tag)[0]

          if (typeof answer !== 'undefined' && answer.to && answer.from) {
            return answer.from !== answer.to
          } else {
            return true
          }
        }
      },
      emitAnswer() {
        this.$emit('answer', {
          type: 'matching',
          answer: this.answers,
          id: this.exercise.id,
          language: this.charSet,
          completed: this.answers.length === this.leftParts.length
        })
      }
    }
  }
</script>

