import Answers from '@/helpers/Answers'

export default {
  props: {
    answer: {
      default: null,
      type: Object,
      required: false
    },
    index: {
      default: 1,
      type: Number,
      required: true
    },
    exercise: {
      default: null,
      type: Object,
      required: true
    },
    totalScore: {
      default: 0,
      type: Number,
      required: true
    }
  },
  computed: {
    userScore() {
      return Answers.getScore(this.exercise, this.answer)
    },
    isAnswer() {
      return this.answer !== null
    }
  }
}
