<template>
  <div>
    <h2 class="h2">Downloads</h2>
    <div>
      <div v-if="downloadsData.type && downloadsData.type !== 'free'" class="row is-justify-space-between">
        <DownloadsItem v-if="downloadsData.downloads.lesson" title="Audio Lesson" :link="downloadsData.downloads.lesson" name="lesson.mp3" :lesson-id="lessonTile" icon="fas fa-download"/>
        <DownloadsItem v-if="downloadsData.downloads.dialogue" title="Audio Dialogue" :link="downloadsData.downloads.dialogue" name="dialogue.mp3" :lesson-id="lessonTile" icon="fas fa-download"/>
        <DownloadsItem v-if="downloadsData.downloads.review" title="Audio Review" :link="downloadsData.downloads.review" name="review.mp3" :lesson-id="lessonTile" icon="fas fa-download"/>
        <DownloadsItem v-if="downloadsData.downloads.pdf1" title="Notes (simp.)" :link="downloadsData.downloads.pdf1" name="notes.pdf" :lesson-id="lessonTile" icon="fas fa-download"/>
        <DownloadsItem v-if="downloadsData.downloads.pdf2" title="Notes (trad.)" :link="downloadsData.downloads.pdf2" name="notes.pdf" :lesson-id="lessonTile" icon="fas fa-download"/>
      </div>
    </div>
  </div>
</template>

<script>
  import DownloadsItem from "./DownloadsItem";
  import camelCase from 'lodash.camelcase';
  import upperFirst from 'lodash.upperfirst';

  export default {
    name: "Downloads",
    components: {DownloadsItem},
    data () {
      return {
        downloadInProgress: false
      }
    },
    props: {
      lessonId: String,
      extra: Boolean,
      title: String,
      downloadsData: Object
    },
    computed: {
      lessonTile () {
        return upperFirst(camelCase(this.title))
      }
    }
  }
</script>

<style scoped>
  i {
    font-size: 40px;
  }
</style>
