<template>
  <div class="exercise-view">
    <keep-alive>
      <div class="exercise-container" ref="container"></div>
    </keep-alive>
    <div class="button-wrapper">
      <base-button type="secondary" @click="submitAnswer" class="text-capitalize text-lg">Next exercise<i class="ml-2 fa fa-arrow-right"></i></base-button>
    </div>
  </div>
</template>

<script>
  import {
    CURRENT_EXERCISE,
    CURRENT_EXERCISE_TYPE,
    ADD_TO_ANSWER_HISTORY
  } from '@/mutation-types'

  import { mapState, mapMutations } from 'vuex'
  import ExerciseElementFactory from '@/helpers/ExerciseElementFactory'
  import ExerciseTypes from '@/helpers/ExerciseTypes'
  import camelCase from 'lodash.camelcase'
  import swal from 'sweetalert2'
  import isNull from 'lodash/isNull'

  export default {
    name: 'ExerciseView',
    data() {
      return {
        exercise: {},
        answer: {}
      }
    },
    computed: {
      isAnswer() {
        return Object.keys(this.answer).length > 0 &&
          this.answer.constructor === Object
      },
      isCompleted() {
        return this.isAnswer && this.answer.completed
      },
      multipleChoiceExercise() {
        if (this.multipleChoiceQuestionData && this.multipleChoiceQuestionCount > 0) {
          return this.multipleChoiceQuestionData
        }

        return null
      },
      reorderSentenceExercise() {
        if (this.reorderSentenceQuestionData && this.reorderSentenceQuestionCount > 0) {
          return this.reorderSentenceQuestionData
        }

        return null
      },
      dictationExercise() {
        if (this.dictationQuestionData && this.dictationQuestionCount > 0) {
          return this.dictationQuestionData
        }

        return null
      },
      matchingExercise() {
        if (this.matchingQuestionData && this.matchingQuestionCount > 0) {
          return this.matchingQuestionData
        }

        return null
      },
      ...mapState({
        charSet: state => state.userInfo.charSet,
        // multipleChoiceExerciseData: state => state.exercises.multipleChoiceExerciseData,
        // sentenceExerciseData: state => state.exercises.sentenceExerciseData,
        // dictationExerciseData: state => state.exercises.dictationExerciseData,
        // matchingExerciseData: state => state.exercises.matchingExerciseData,
        currentExercise: state => state.exercises.currentExercise,
        currentExerciseType: state => state.exercises.currentExerciseType,
        multipleChoiceQuestionData: state => state.exercises.multipleChoiceQuestionData,
        reorderSentenceQuestionData: state => state.exercises.sentenceQuestionData,
        dictationQuestionData: state => state.exercises.dictationQuestionData,
        matchingQuestionData: state => state.exercises.matchingQuestionData,
        multipleChoiceQuestionCount: state => state.exercises.multipleChoiceQuestionCount,
        reorderSentenceQuestionCount: state => state.exercises.sentenceQuestionCount,
        dictationQuestionCount: state => state.exercises.dictationQuestionCount,
        matchingQuestionCount: state => state.exercises.matchingQuestionCount
      })
    },
    mounted() {
      const type = camelCase(this.currentExerciseType);

      if (this[`${type}Exercise`] !== null) {
        this.exercise = this[`${type}Exercise`][this.currentExercise];
        this.createExerciseElement()
      } else {
        this.$watch(`${type}Exercise`, () => {
          this.exercise = this[`${type}Exercise`][this.currentExercise];
          this.createExerciseElement()
        })
      }
    },
    methods: {
      createExerciseElement() {
        this.removeOldElements();

        const instance = ExerciseElementFactory.getElement(
          this.currentExerciseType,
          {
            exercise: this.exercise,
            index: this.currentExercise + 1,
            totalScore: this.countTotalScore()
          }
        );

        instance.$on('answer', this.answerEvent);

        instance.$on('submitAnswer', this.submitAnswer);

        instance.$mount();
        this.$refs.container.appendChild(instance.$el)
      },
      removeOldElements() {
        var myNode = this.$refs.container;
        var firstChild = myNode.firstChild;

        while(firstChild) {
          myNode.removeChild(firstChild);
          firstChild = myNode.firstChild;
        }
      },
      countTotalScore() {
        let score = 0;

        const exercises = this[`${camelCase(this.currentExerciseType)}Exercise`];

        for (let exercise of exercises) {
          score += Number.parseInt(exercise.score)
        }

        return score
      },
      submitAnswer() {
        if (this.isCompleted) {
          this.addToHistory(this.answer);
          this.getNextExercise();
          this.answer = {}
        } else {
          swal.fire({
            title: 'Are you sure?',
            text: "You haven't finished this exercise!",
            type: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Skip'
          }).then((result) => {
            if (result.value) {
              if (!this.answer.answer){
                switch (this.currentExerciseType) {
                  case 'multiple-choice':
                    this.answer.answer = [0];
                    break;
                  case 'matching':
                    this.answer.answer = [];
                    Array.from({length: 5}).forEach((x, index) => this.answer.answer.push({from: index + 1, to: index === 4 ? 1 : index + 2}));
                    break;
                  case 'reorder-sentence':
                    this.answer.answer = this.exercise.question.segments;
                    this.answer.answer.unshift(this.answer.answer.pop());
                    break;
                  case 'dictation':
                    this.answer.answer = 'Not Filled';
                    break;
                }
              } else {
                if(this.currentExerciseType === 'matching') {
                  Array.from({length: 5 - this.answer.answer.length}).forEach((x, index) => this.answer.answer.push({from: index + 1, to: index === 4 ? 1 : index + 2}));
                }
              }
              let mergedData = {...this.answer, ...{
                    type: this.currentExerciseType,
                    completed: true,
                    language: this.charSet,
                    id: this.exercise.id
                  }};
              this.addToHistory(mergedData);
              this.getNextExercise();
              this.answer = {}
            }
          });
        }
      },
      getNextExercise() {
        const type = camelCase(this.currentExerciseType);

        if (this[`${type}Exercise`] !== null && this.currentExercise < this[`${type}Exercise`].length - 1) {
          this.setExerciseForCurrentType(type)
        } else {
          this.setExerciseForNextType()
        }
      },
      setExerciseForCurrentType(type) {
        this.setCurrentExercise(this.currentExercise + 1);
        this.exercise = this[`${type}Exercise`][this.currentExercise];

        this.createExerciseElement()
      },
      setExerciseForNextType() {
        let nextType = ExerciseTypes.getNextType(this.currentExerciseType);
        let nextTypeCamelCase = camelCase(nextType);

        while (this[`${nextTypeCamelCase}QuestionCount`] === 0 && nextType !== '') {
          nextType = ExerciseTypes.getNextType(nextType);
          nextTypeCamelCase = camelCase(nextType);
        }

        if (nextType !== '' && this[`${nextTypeCamelCase}QuestionCount`] > 0) {
          this.setCurrentExercise(0);
          this.setCurrentExerciseType(nextType);

          this.exercise = this[`${nextTypeCamelCase}Exercise`][this.currentExercise];

          this.createExerciseElement()
        } else {
          this.$emit('finished')
        }
      },
      answerEvent(answer) {
        this.answer = answer;

        if (this.answer.type === 'multiple-choice') {
          this.submitAnswer()
        }
      },
      ...mapMutations('exercises', {
        setCurrentExercise: CURRENT_EXERCISE,
        setCurrentExerciseType: CURRENT_EXERCISE_TYPE,
        addToHistory: ADD_TO_ANSWER_HISTORY
      })
    }
  }
</script>

<style lang="scss" scoped>
  @import "@/assets/sass/exercises/_button.scss";

  .button-wrapper {
    .button {
      text-transform: uppercase;
      border: 0;

      span {
        margin-right: 0.6em;
      }
    }
  }
</style>
