<template>
  <div class="exercise-block">
    <exercise-header
      title="Listen and type"
      :index="index"
      :is-answer="isAnswer"
      :total-score="totalScore"
      :exercise-score="exercise.score"
      :user-score="userScore"
      show-exercise-number="show-exercise-number"
      remove-padding-from-score="remove-padding-from-score"></exercise-header>
    <div
      class="sentence row"
      :class="{
      'is-answer': isAnswer,
      'correct': isAnswer && isCorrect(solution),
      'wrong': isAnswer && isWrong(solution)
      }">
      <base-input class="mb-3 col-12" group="group">
        <div class="input-group-prepend" id="button-addon1">
          <button class="btn btn-outline-primary" type="button" @click="play">
            <i class="fa ni-lg" ref="icon" :class="icon"></i>
          </button>
          <audio ref="audio" :src="audioUrl"></audio>
        </div>
        <input
          class="form-control"
          type="text"
          v-model="solution"
          @input="emitAnswer"
          @keyup.enter="submitAnswer"
          :disabled="isAnswer"
          :placeholder="placeholder"
          autofocus
          ref="input"
          aria-label="Listening Practice Input"
          aria-describedby="button-addon1"/>
      </base-input>
      <div class="col-12" v-if="isAnswer && isWrong(solution)">
        <card class="bg-danger">
          <p class="text-white characters">{{ exercise.answer[charSet.slice(0,1)] }}</p>
          <p v-if="charSet !== 'pinyin' && pinyin" class="text-white pinyin">{{ exercise.answer['p'] }}</p>
        </card>
      </div>
    </div>
    <p class="exercise-description exercise-description--small" v-if="!isAnswer">
      To type in Chinese, you'll need to first install a Chinese keyboard on your phone or computer.
      To write in Pinyin, you can use an English keyboard and use numbers for tones. e.g. ni3hao3</p>
  </div>
</template>

<script>
  import { mapState } from 'vuex'
  import debounce from 'lodash.debounce'
  import LanguageNumber from '@/mixins/LanguageNumber'
  import Exercise from '@/mixins/Exercise'
  import ExerciseHeader from '@/components/Exercises/ExerciseHeader'

  export default {
    name: "DictationExercise",
    components: {
      ExerciseHeader
    },
    mixins: [LanguageNumber, Exercise],
    data() {
      return {
        icon: 'fa fa-play',
        solution: ''
      }
    },
    computed: {
      audioUrl() {
        return this.exercise.question.audio
      },
      placeholder() {
        if (this.charSet === 'pinyin') {
          return 'Type using Pinyin...'
        } else {
          return 'Type-in what you hear...'
        }
      },
      correctAnswer() {
        return this.exercise.answer
      },
      ...mapState({
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet
      })
    },
    created() {
      this.emitAnswer = debounce(() => {
        this.$emit('answer', {
          type: 'dictation',
          answer: this.solution ? this.solution : null,
          id: this.exercise.id,
          language: this.charSet,
          completed: this.solution.length > 0
        })
      }, 100)
    },
    mounted() {
      if (this.isAnswer) {
        this.solution = this.answer.answer
      } else {
        this.play();
      }

      // remove "stop" class when audio playing has ended
      this.$refs.audio.addEventListener('ended', () => {
        this.toggleIcon();
        this.$refs.input.focus();
      })
    },
    methods: {
      toggleIcon() {
        this.icon = this.icon === 'fa fa-play' ? 'fa fa-pause' : 'fa fa-play'
      },
      play() {
        const audioElement = this.$refs.audio;

        if (audioElement.duration > 0 && !audioElement.paused) {
          audioElement.pause();
          audioElement.load();

          this.toggleIcon()
        } else {
          audioElement.play();

          this.toggleIcon()
        }
      },
      isCorrect() {
        return this.userScore > 0
      },
      isWrong() {
        return !this.isCorrect()
      },
      submitAnswer () {
        this.$emit('submitAnswer');
      }
    }
  }
</script>

