  @import "@/assets/sass/exercises/_exercise.scss";

  $button-height: 3em;
  $button-width: $button-height * 2;
  $button-border-radius: .4em;

  .characters {
    font-size: 30px;
  }
  .pinyin, .english {
    font-size: 20px;
  }

  .sentence {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-bottom: 1em;

    &.is-answer {
      .answer {
        color: black;
      }

      &.correct {
        .answer {
          background: #beffc3;
        }
      }

      &.wrong {
        .button {
          border-bottom-left-radius: 0;
        }

        .answer {
          background: #fdbfbf;
          border-bottom-right-radius: 0;
        }
      }
    }
  }

  .exercise-description {
    &--small {
      font-size: .8em;
    }
  }

  .button {
    width: $button-width;
    height: $button-height;

    background: #eeeeee;
    border: 1px solid #bbbbbb;
    border-top-left-radius: $button-border-radius;
    border-bottom-left-radius: $button-border-radius;

    cursor: pointer;
  }

  .answer {
    width: calc(100% - #{$button-width});
    padding: 0.3em 0.5em;

    border: 1px solid #bbbbbb;
    border-left: 0;
    border-top-right-radius: $button-border-radius;
    border-bottom-right-radius: $button-border-radius;
  }

  .correct-answer {
    padding: 0.7em 1em;
    margin-bottom: 1em;
    width: 100%;

    border: 1px solid #aaa;
    border-top: 0;
    border-radius: 0.3em;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
    background: #beffc3;
  }
</style>
