import Vue from 'vue'
import Vuex from 'vuex'
import VuexPersistence from 'vuex-persist'

import exercises from './module/exercises'

Vue.use(Vuex);


const initialState = {
  isUserAuthenticated: false,
  token: '',
  userInfo: {
    pinyin: false,
    banner: true,
    charSet: 'simplified',
    upgrade: {upgradePath: 0}},
  userStats: {},
  mainCourse:  {},
  courses:  [],
  courseData: [],
  bookmarkData: [],
  historyData: [],
  latestData: [],
  moreCourses: [],
  currentLesson: {},
  nextLessons: false
};

export default new Vuex.Store({
  name: 'state',
  modules: {exercises},
  plugins: [new VuexPersistence().plugin],
  state: initialState,
  mutations: {
    SET_TOKEN (state, token) {
      state.token = token;
      Vue.axios.defaults.headers.common['Authorization'] = `Bearer ${token}`
    },
    APPLY_TOKEN (state) {
      Vue.axios.defaults.headers.common['Authorization'] = `Bearer ${state.token}`
    },
    RESET_STATE (state) {
      Object.keys(state).forEach(key => {
        state[key] = initialState[key]
      });
      console.log(initialState);
    },
    RESET_USER_INFO (state) {
      delete state.userInfo;
      state.userInfo = {
        pinyin: false,
        banner: true,
        charSet: 'simplified',
        upgrade: {upgradePath: 0}
      }
    },
    TOGGLE_BANNER (state, show) {
      state.userInfo.banner = show
    },
    SET_IS_USER_AUTHENTICATED (state, isUserAuthenticated) {
      state.isUserAuthenticated = isUserAuthenticated
    },
    SET_USER_INFO (state, userInfo) {
      state.userInfo = {...state.userInfo, ...userInfo}
    },
    SET_USER_STATS (state, userInfo) {
      state.userStats = {...state.userStats, ...userInfo}
    },
    SET_USER_LEVEL (state, level) {
      state.userInfo.level = level
    },
    SET_USER_CHARSET (state, charSet) {
      state.userInfo.charSet = charSet
    },
    TOGGLE_PINYIN (state) {
      state.userInfo.pinyin = !state.userInfo.pinyin
    },
    SET_CURRENT_LESSON (state, lessonData) {
      state.currentLesson = lessonData
    },
    TOGGLE_CURRENT_LESSON_STUDIED (state, studied) {
      state.currentLesson.studied = studied
    },
    TOGGLE_CURRENT_LESSON_SAVED (state, saved) {
      state.currentLesson.saved = saved
    },
    SET_MAIN_COURSE (state, mainCourse) {
      state.mainCourse = mainCourse
    },
    SET_USER_COURSES (state, lessonData) {
      state.courses = lessonData
    },
    SET_COURSE_DATA (state, lessonData) {
      state.courseData = lessonData
    },
    ADD_COURSE_DATA (state, lessonData) {
      state.courseData.push(lessonData)
    },
    REMOVE_COURSE_DATA (state, lessonData) {

    },
    SET_STUDIED_LESSONS (state, lessonData) {
      state.historyData = lessonData
    },
    SET_LATEST_LESSONS (state, lessonData) {
      state.latestData = lessonData
    },
    SET_BOOKMARKED_LESSONS (state, lessonData) {
      state.bookmarkData = lessonData
    },
    SET_MORE_COURSES (state, moreCourses) {
      state.moreCourses = moreCourses
    }
  },
  actions: {
    getUserInfo (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/get-info')
          .then(({data}) => {
            context.commit('SET_USER_INFO', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    getUserStats (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/get-stats')
          .then(({data}) => {
            context.commit('SET_USER_STATS', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    changeLevel (context, level) {
      return new Promise((resolve, reject) => {
        context.commit('SET_USER_LEVEL', level);
        Vue.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/onboarding/level', {level: level})
          .then(() => {
            context.dispatch('getUserInfo');
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    changeCharSet (context, charSet) {
      return new Promise((resolve, reject) => {
        context.commit('SET_USER_CHARSET', charSet);
        Vue.axios.put(process.env.VUE_APP_ROOT_API + '/api/v1/onboarding/level', {charSet: charSet})
          .then(() => resolve())
          .catch(error => reject(error));
      })
    },
    currentLesson (context, lessonData) {
      return new Promise((resolve, reject) => {
        context.commit('SET_CURRENT_LESSON', lessonData);
        resolve();
      })
    },
    toggleCurrentLessonStudied (context, payload) {
      return new Promise((resolve, reject) => {
        context.commit('TOGGLE_CURRENT_LESSON_STUDIED', payload.studied);
        Vue.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-studied', {
          lessonId: payload.lessonId,
          status: payload.studied
        }).then(() => resolve())
          .catch(() => {reject()});
      })
    },
    toggleCurrentLessonSaved (context, payload) {
      return new Promise((resolve, reject) => {
        context.commit('TOGGLE_CURRENT_LESSON_SAVED', payload.saved);
        Vue.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-saved', {
          lessonId: payload.lessonId,
          status: payload.saved
        }).then(() => {resolve()})
          .catch(() => {reject()});
      })
    },
    getUserCourses (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/user-courses', {
          headers: {
            Authorization: `Bearer ${context.state.token}`
          }
        })
          .then(({data}) => {
            context.commit('SET_USER_COURSES', data);
            resolve()
          })
          .catch(() => {reject()});
      })
    },
    buildMainCourse (context) {
      return new Promise(async (resolve, reject) => {
        // IF NO COURSES - GET DUMMY COURSE
        if (context.courses.length === 0 && context.bookmarkData.length > 0) {
          context.commit('SET_MAIN_COURSE', {
            course: {course_title: 'Your Bookmarked Lessons', bookmarks: true},
            lessonData: context.bookmarkData
          });
          resolve()
        } else if (context.courses.length > 0) {
          let lessonData = await this.getCourseData(context.courses[0]['course'].id);
          if (lessonData.length > 0) {
            context.commit('SET_MAIN_COURSE', {
              course: context.courses[0]['course'],
              lessonData: lessonData
            });
            resolve()
          } else if (context.courses.length > 1) {
            lessonData = await this.getCourseData(context.courses[1]['course'].id);
            if (lessonData.length > 0) {
              context.commit('SET_MAIN_COURSE', {
                course: context.courses[1]['course'],
                lessonData: lessonData
              });
              resolve()
            }
          } else if (context.courses.length > 2) {
            lessonData = await this.getCourseData(context.courses[2]['course'].id);
            if (lessonData.length > 0) {
              context.commit('SET_MAIN_COURSE', {
                course: context.courses[2]['course'],
                lessonData: lessonData
              });
              resolve()
            }
          }
        }
      })
    },
    getMainCourse (context, courseId) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/course-lessons?courseId=${courseId}`)
          .then(({data}) => {
            context.commit('SET_MAIN_COURSE', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    getMoreCourses (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/more-courses`)
          .then(({data}) => {context.commit('SET_MORE_COURSES', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    updateLatestLessons (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/all-lessons?limit=20')
          .then(({data}) => {context.commit('SET_LATEST_LESSONS', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    updateBookmarks (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/bookmarks?limit=30')
          .then(({data}) => {
            context.commit('SET_BOOKMARKED_LESSONS', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    updateHistory (context) {
      return new Promise((resolve, reject) => {
        Vue.axios.get(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/history?limit=30')
          .then(({data}) => {
            context.commit('SET_STUDIED_LESSONS', data);
            resolve()
          })
          .catch(error => reject(error));
      })
    },
    dropThisCourse(context, payload) {
      if (payload.area === 'main') {
        if (context.state.courseData.length > 0) {
          context.commit('SET_MAIN_COURSE', context.state.courseData[0]);
          if (context.state.courseData.length > 1) {
            context.commit('SET_COURSE_DATA', context.state.courseData.slice(1, 5))
          } else {
            context.commit('SET_COURSE_DATA', [])
          }
        } else if (this.bookmarkData) {
          context.commit('SET_MAIN_COURSE',
            {course: {course_title: 'Your Bookmarked Lessons'}, lessonData: context.state.bookmarkData});
        } else {
          context.commit('SET_MAIN_COURSE', {})
        }
      } else if (payload.area === 'course') {
        context.commit('SET_COURSE_DATA',
          context.state.courseData.filter((course) => {return course.course.id !== payload.courseId}));
      }
    },
    async getToken(context) {
      await Vue.axios.post(process.env.VUE_APP_ROOT_API + '/api/v1/token', {
        key: process.env.VUE_APP_SECRET,
        type: 'dashboard'
      })
        .then(({data}) => context.commit('SET_TOKEN', data.token))
    }
  }
})
