<template>
  <div class="exercise-block">
    <exercise-header title="Choose the right option" :index="index" :is-answer="isAnswer" :total-score="totalScore" :exercise-score="exercise.score" :user-score="userScore"></exercise-header>
    <div class="sentence-to-fill">
      <div class="chinese" v-html="title"></div>
      <div class="pinyin" v-if="pinyin">{{ exercise.question['p'] }}</div>
    </div>
    <div class="answers">
      <div v-for="(choice, index) in choices" :key="index" class="col-12 col-sm-6">
        <base-button
          :type="isCorrect(choice.id) ? 'success' : isWrong(choice.id) ? 'danger' : 'light'"
          :class="[{
          'is-answer': isAnswer,
          'selected': isSelected(choice.id),
          }, 'btn-block', 'mb-2']"
          @click="select(choice.id)"
          :disabled="isAnswer">
          <div class="row">
            <div class="col-12 characters">{{ choice[charSet.slice(0,1)] }}</div>
            <div class="col-12 pinyin text-white" v-if="charSet !== 'pinyin' && pinyin">{{ choice['p'] }}</div>
          </div>
        </base-button>
      </div>
    </div>
  </div>
</template>

<script>
  import { mapState } from 'vuex'

  import LanguageNumber from '@/mixins/LanguageNumber'
  import Exercise from '@/mixins/Exercise'
  import ExerciseHeader from '@/components/Exercises/ExerciseHeader'

  export default {
    name: "MultipleChoiceExercise",
    components: {
      ExerciseHeader
    },
    mixins: [LanguageNumber, Exercise],
    data() {
      return {
        selected: [],
        isMultipleChoice: false
      }
    },
    computed: {
      title() {
        const title = this.exercise.question[this.charSet.slice(0,1)];

        return title.replace(/([_]+)/g, '<span class="empty-space"></span>')
      },
      choices() {
        return this.exercise.question.choices
      },
      ...mapState({
        pinyin: state => state.userInfo.pinyin,
        charSet: state => state.userInfo.charSet
      })
    },
    methods: {
      isCorrect(tag) {
        if (this.answer !== null && this.answer.answer.length > 0) {
          return this.exercise.answer.id === tag
        }
        return false
      },
      isWrong(tag) {
        if (this.answer !== null && this.answer.answer.length > 0) {
          return this.exercise.answer.id !== tag && this.answer.answer[0] === tag
        }
        return false
      },
      isSelected(tag) {
        return this.selected.includes(tag)
      },
      select(tag) {
        if (this.selected.includes(tag)) {
          this.selected = this.selected.filter( x => x !== tag )
        } else {
          if (this.isMultipleChoice) {
            this.selected.push(tag)
          } else {
            this.selected = [tag]
          }
        }
        this.emitAnswer()
      },
      emitAnswer() {
        this.$emit('answer', {
          type: 'multiple-choice',
          answer: this.selected,
          id: this.exercise.id,
          language: this.charSet,
          completed: this.selected.length > 0
        })
      }
    }
  }
</script>

