<template>
  <div class="col-12">
    <vue-plyr ref="plyr" id="audio-player" v-show="false">
      <audio :src="audio"></audio>
    </vue-plyr>
    <div v-if="playerInitialized" class="row">
      <div class="col-12 col-lg-2 text-center">
        <div class="row">
          <div class="col-4 p-0 align-self-center">
            <base-button @click="player.rewind()" class="border-0 bg-transparent shadow-none btn-lg m-0">
              <svg width="30" height="30" viewBox="0 0 30 30" class="white">
                <path fillRule="evenodd" clipRule="evenodd" d="M14.989 4.16232V0L4.98901 6H13.0714V6.00104C19.109 6.08286 23.978 10.9759 23.978 17C23.978 23.0751 19.0261 28 12.9176 28C7.4959 28 2.98529 24.1202 2.03961 19H0C0.967285 25.2302 6.38239 30 12.9176 30C20.1368 30 25.989 24.1797 25.989 17C25.989 10.5213 21.2238 5.14957 14.989 4.16232Z" transform="translate(2)"></path>
                <path fillRule="evenodd" clipRule="evenodd" d="M3.476 7.821V0.121H0.363L-1.79291e-07 1.683H1.54V7.821H3.476ZM8.92 7.942C6.951 7.942 5.785 6.347 5.785 3.971C5.785 1.584 6.951 -9.91821e-08 8.92 -9.91821e-08C10.878 -9.91821e-08 12.022 1.584 12.022 3.971C12.022 6.347 10.878 7.942 8.92 7.942ZM8.92 6.281C9.624 6.281 10.086 5.533 10.086 3.971C10.086 2.42 9.624 1.661 8.92 1.661C8.216 1.661 7.743 2.42 7.743 3.971C7.743 5.533 8.216 6.281 8.92 6.281Z" transform="translate(9.07812 13.179)"></path>
              </svg>
            </base-button>
          </div>
          <div class="col-4 p-0">
            <base-button @click="player.togglePlay()" class="border-0 bg-transparent shadow-none btn-lg m-0 p-0">
              <div v-show="playing">
                <svg id="pause" width="30" height="30" viewBox="0 0 40 40" class="white">
                  <path d="M10 0H0V40H10V0Z" transform="translate(24)"></path>
                  <path d="M10 0H0V40H10V0Z" transform="translate(6)"></path>
                </svg>
              </div>
              <div v-show="!playing">
                <svg id="play" width="30" height="30" viewBox="0 0 40 40" class="white" fill="#444444">
                  <path fillRule="evenodd" clipRule="evenodd" d="M0 40V0L33.6 20L0 40Z" transform="translate(3)"></path>
                </svg>
              </div>
            </base-button>
          </div>
          <div class="col-4 p-0 align-self-center">
            <base-button @click="player.forward()" class="border-0  bg-transparent shadow-none btn-lg m-0">
              <svg width="30" height="30" viewBox="0 0 30 30" class="white">
                <path fillRule="evenodd" clipRule="evenodd" d="M14.989 4.16232V0L4.98901 6H13.0714V6.00104C19.109 6.08286 23.978 10.9759 23.978 17C23.978 23.0751 19.0261 28 12.9176 28C7.4959 28 2.98529 24.1202 2.03961 19H0C0.967285 25.2302 6.38239 30 12.9176 30C20.1368 30 25.989 24.1797 25.989 17C25.989 10.5213 21.2238 5.14957 14.989 4.16232Z" transform="translate(27.989) scale(-1 1)"></path>
                <path fillRule="evenodd" clipRule="evenodd" d="M3.476 7.821V0.121H0.363L-1.79291e-07 1.683H1.54V7.821H3.476ZM8.92 7.942C6.951 7.942 5.785 6.347 5.785 3.971C5.785 1.584 6.951 -9.91821e-08 8.92 -9.91821e-08C10.878 -9.91821e-08 12.022 1.584 12.022 3.971C12.022 6.347 10.878 7.942 8.92 7.942ZM8.92 6.281C9.624 6.281 10.086 5.533 10.086 3.971C10.086 2.42 9.624 1.661 8.92 1.661C8.216 1.661 7.743 2.42 7.743 3.971C7.743 5.533 8.216 6.281 8.92 6.281Z" transform="translate(9.07812 13.179)"></path>
              </svg>
            </base-button>
          </div>
        </div>
      </div>
      <div class="col-12 col-lg-9 text-center align-self-center pt-3 pt-md-5 pt-lg-0">
        <div class="row">
          <div class="col-12 col-lg-2 text-white align-self-center">
            <div>
              <span class="">{{ currentLocation }} / {{ maxLocation }}</span>
            </div>
          </div>
          <div class="col-12 col-lg-10 align-self-center">
            <audio-progress :value="progress" @changed="player.currentTime = $event/100 * duration"></audio-progress>
          </div>
        </div>
      </div>
      <div class="d-none d-lg-block col-lg-1 text-left align-self-center">
        <base-button @click="player.muted = !muted" :title="muted ? 'Unmute' : 'Mute'" :aria-label="muted ? 'Unmute' : 'Mute'" class="border-0 bg-transparent shadow-none btn-lg">
          <i class="fas text-lg" :class="muted ? 'fa-volume-mute' : 'fa-volume-up'"></i>
        </base-button>
      </div>
    </div>
  </div>
</template>

<script>
  import moment from 'moment';
  import AudioProgress from "../../components/AudioProgress";

  export default {
    name: "AudioPlayer",
    components: {AudioProgress},
    props: {
      audio: String,
      title: String
    },
    data () {
      return {
        playerInitialized: false,
        playing: false,
        location: 0,
        duration: 0,
        muted: false
      }
    },
    computed: {
      currentLocation () {return moment(this.location * 1000).format('mm:ss')},
      maxLocation () {return moment(this.duration * 1000).format('mm:ss')},
      player () { return this.$refs.plyr.player },
      progress () { return this.location / this.duration * 100 }
    },
    methods: {
    },
    mounted() {
      this.player.on('ready', () => {
        this.playerInitialized = true;
      });
      this.player.on('playing', () => {
        this.playing = true;
        this.duration = this.player.duration;

        if ('mediaSession' in navigator) {
          navigator.mediaSession.metadata = new MediaMetadata({
            title: this.title,
            artist: 'ChinesePod',
            album: `Podcast Series`,
            artwork: [
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '64x64',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_64.png',  sizes: '96x96',   type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '120x120', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_120.png', sizes: '128x128', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '192x192', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_192.png', sizes: '256x256', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_384.png', sizes: '384x384', type: 'image/png' },
              { src: 'https://www.chinesepod.com/home/img/icons/icon_512.png', sizes: '512x512', type: 'image/png' }
            ]
          });
          navigator.mediaSession.playbackState = 'playing';
          navigator.mediaSession.setActionHandler('play', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('pause', () => this.player.togglePlay());
          navigator.mediaSession.setActionHandler('seekbackward', () => this.player.rewind());
          navigator.mediaSession.setActionHandler('seekforward', () => this.player.forward());
          // navigator.mediaSession.setActionHandler('previoustrack', function() {});
          // navigator.mediaSession.setActionHandler('nexttrack', function() {});
        }
      });
      this.player.on('pause', () => {
        this.playing = false;
        if ('mediaSession' in navigator) {
          navigator.mediaSession.playbackState = "paused";
        }
      });
      this.player.on('ended', () => {
        this.playing = false;
      });
      this.player.on('timeupdate', () => {
        this.location = this.player.currentTime
      });
      this.player.on('volumechange', () => {
        this.muted = this.player.muted
      });
      this.player.on('loadedmetadata', () => {
        this.duration = this.player.duration;
      });
    }
  }
</script>

<style scoped>
  .white {
    fill: white;
  }
  #play, #pause {
    height: 70px;
    width: 70px;
  }

</style>
