<template>
  <div>
    <base-header class="pb-md-6" type="">
      <div class="row align-items-center py-4">
        <div class="col-9 col-lg-10">
          <router-link to="/home" class="d-inline-block d-md-none mr-2"><i class="fas fa-home"></i> - Dashboard</router-link>
          <h1 class="h2 d-inline-block mb-0 mr-md-4">Course List</h1>
          <nav aria-label="breadcrumb" class="d-none d-md-inline-block">
            <ol class="breadcrumb breadcrumb-links">
              <li class="breadcrumb-item"><router-link to="/"><i class="fas fa-home"></i></router-link></li>
              <li class="breadcrumb-item"><router-link to="/home">Dashboard</router-link></li>
              <li class="breadcrumb-item active" aria-current="page">Courses</li>
            </ol>
          </nav>
        </div>
      </div>
    </base-header>
    <!--    COURSE CONTENT-->
    <div class="container-fluid mt-md--6">
      <div class="row">
        <div class="col-12">
          <div class="row">
            <div class="col-sm-8 offset-sm-2">
              <div v-if="courseListData.length < 1" class="col-12 d-flex align-items-center pt-4 pt-sm-0 lesson-loading">
                <i class="fas fa-spinner fa-spin mx-auto"></i>
              </div>
            </div>
          </div>
          <div class="row">
            <div v-for="course in courseListData" :key="course.id" class="col-6 col-md-2">
              <course-card  :course="course" />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import preloaderSquare from '@/assets/img/preloader-square.svg'
  import {mapState} from 'vuex'
  import CourseCard from "../../components/Dashboard/CourseCard";

  export default {
    name: "CourseList",
    components: {CourseCard},
    title () {`Lesson Courses | ChinesePod`},
    data () {
      return {
        courseListData: [],
        preloaderSquare: preloaderSquare,
        enrolled: false
      }
    },
    computed: {
      ...mapState({
        courses: 'courses'
      })
    },
    methods: {
      async getCourses () {
        this.courseListData = await this.axios.get(process.env.VUE_APP_ROOT_API + `/api/v1/dashboard/all-courses`)
          .then(({data}) => {
            data.forEach((course) => {
              course.enrolled = this.courses.filter((item) => item.course.id === course.id).length > 0
            });
            return data
          })
          .catch(() => {window.location.href = '/home'});
      }
    },
    async beforeMount () {
      await this.getCourses();
    }
  }
</script>

