<template>
  <span>
    <el-popover
      v-if="typeof character === 'object'"
      placement="top"
      ref="charCard"
      trigger="hover"
    >
      <div class="character mb-3">
        <div v-html="charCard"></div>
      </div>
      <p class="pinyin text-left" v-html="pinyin"></p>
      <p class="english text-left" v-html="english"></p>
      <span class="transcript align-middle" slot="reference" v-html="typeof character === 'object' ? charOutput : character"></span>
    </el-popover>
    <span v-else >{{character}}</span>
  </span>
</template>

<script>
  import convertPinyinTones from 'pinyin-tone-converter';

  export default {
    name: "CharacterItem",
    props: {
      character: {
        type: [Object, String]
      },
      type: {
        type: String,
        default: 'simplified'
      },
      popup: {
        type: Boolean,
        default: false
      },
      characterIndex: Number,
      sentenceIndex: Number,
      tab: String
    },
    data() {
      return {
        animateChar: true
      }
    },
    computed: {
      charSet() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.charSet : 'simplified'
      },
      charCard() {
        return typeof this.character === 'object' ? this.character[this.charSet.slice(0,1)] : this.character
      },
      charOutput () {
        switch (this.type) {
          case 'simplified':
            return this.character['s'];
          case 'traditional':
            return this.character['t'];
          case 'pinyin':
            return ' ' + convertPinyinTones(this.character['p']);
          default:
            return this.character['s'];
        }
      },
      pinyin () {
        return typeof this.character === 'object' ? convertPinyinTones(this.character['p']) : '';
      },
      english () {
        return this.character.en.split(`\\&#039;`).join('\'')
      }
    },
    methods: {
    }
  }
</script>

<style lang="scss" scoped>
  .character {
    font-size: 48px;
    font-family: 'Noto Sans SC', sans-serif !important;
  }
  .pinyin, .english {
    font-size: 32px;
  }
  .pinyin, .transcript {
    font-family: 'Noto Sans SC', sans-serif !important;
  }
  .transcript {
    &:hover {
      border-radius: 10%;
      background-color: #fff;
      box-shadow: 0 0 5px 2px #E1001E;
    }
  }
  .emphasize-dark {
    text-shadow: 0 0 5px black;
  }
</style>
