<template>
  <div class="col-4 col-md-5 col-lg-3 offset-lg-2">
    <div class="row">
      <div class="col-12 col-md-auto">
        <base-button :type="pinyin ? 'default' : 'secondary'" size="sm" @click="togglePinyin">{{ pinyin ? 'Hide Pinyin' : 'Show Pinyin' }}</base-button>
      </div>
      <div class="col-12 col-md-auto mt-1 mt-md-0">
        <base-button size="sm" @click="toggleCharSet">{{charSet === 'simplified' ? 'Simp: 汉字' : 'Trad: 漢字'}}</base-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from 'vuex'
import {
  TOGGLE_PINYIN
} from '@/mutation-types'

export default {
  name: "Settings",
  data() {
    return {
      show: false
    }
  },
  computed: {
    ...mapState({
      pinyin: state => state.userInfo.pinyin,
      charSet: state => state.userInfo.charSet
    })
  },
  methods: {
    async toggleCharSet () {
      await this.$store.dispatch('changeCharSet', this.charSet === 'simplified' ? 'traditional' : 'simplified')
    },
    ...mapMutations({
      togglePinyin: TOGGLE_PINYIN
    })
  }
}
</script>

