<template>
  <base-nav
    container-classes="container-fluid"
    class="navbar-top border-bottom navbar-expand"
    :class="{'bg-success navbar-dark': type === 'default'}"
  >
    <!-- Navbar links -->
    <ul class="navbar-nav align-items-center">
      <li class="nav-item d-xl-none">
        <!-- Sidenav toggler -->
        <div class="pr-3 sidenav-toggler"
             :class="{active: $sidebar.showSidebar, 'sidenav-toggler-dark': type === 'default', 'sidenav-toggler-light': type === 'light'}"
             @click="toggleSidebar">
          <div class="sidenav-toggler-inner">
            <i class="sidenav-toggler-line"></i>
            <i class="sidenav-toggler-line"></i>
            <i class="sidenav-toggler-line"></i>
          </div>
        </div>
      </li>
      <!-- Search form -->
      <form class="navbar-search form-inline mr-sm-3"
            @submit.prevent="searchLesson"
            :class="{'navbar-search-light': type === 'default', 'navbar-search-dark': type === 'light'}"
            id="navbar-search-main">
        <div class="form-group mb-0">
          <div class="input-group input-group-alternative input-group-merge">
            <div class="input-group-prepend">
              <span class="input-group-text"><i class="fas fa-search"></i></span>
            </div>
            <input class="form-control" placeholder="Search" type="text" v-model.trim="searchQuery">
          </div>
        </div>
        <button type="button" class="close" data-action="search-close" data-target="#navbar-search-main" aria-label="Close">
          <span aria-hidden="true">×</span>
        </button>
      </form>
    </ul>
    <div class="ml-auto">
      <base-dropdown hasToggle menuOnRight>
        <div slot="title-container" type="primary">
          <a class="nav-link pr-0" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
            <div class="media align-items-center">
                  <span class="avatar avatar-sm rounded-circle">
                    <img class="bg-white" alt="Image placeholder" :src="userAvatar" onerror="this.src='/dash/img/brand/symbol-black-center.svg'">
                  </span>
              <div class="media-body ml-2">
                <span class="mb-0 text-sm font-weight-bold" :class="type === 'default' ? 'text-white' : ''">{{ userName }}</span>
              </div>
            </div>
          </a>
        </div>
        <div class="dropdown-header noti-title">
          <h6 class="text-overflow m-0">Welcome!</h6>
        </div>
        <a href="/accounts/settings/profile/" class="dropdown-item">
          <i class="ni ni-single-02"></i>
          <span>My profile</span>
        </a>
        <a href="/accounts/settings/lesson/" class="dropdown-item">
          <i class="ni ni-settings-gear-65"></i>
          <span>Settings</span>
        </a>
        <a href="https://support.chinesepod.com/" class="dropdown-item">
          <i class="ni ni-support-16"></i>
          <span>Support</span>
        </a>
        <a href="/blog/the-chinesepod-tour/" class="dropdown-item" >
          <i class="ni ni-compass-04"></i>
          <span>Tutorials</span>
        </a>
        <router-link class="dropdown-item" to="/feedback">
          <i class="fas fa-comments"></i>
          <span>Report Issue</span>
        </router-link>
        <div class="dropdown-divider"></div>
        <a @click="logout()" class="dropdown-item">
          <i class="ni ni-user-run"></i>
          <span>Logout</span>
        </a>
      </base-dropdown>
    </div>
  </base-nav>
</template>
<script>
  import { BaseNav, BaseDropdown } from '@/components';

  export default {
    components: {
      // CollapseTransition,
      BaseNav,
      BaseDropdown
      // Modal
    },
    props: {
      type: {
        type: String,
        default: 'default', // default|light
        description: 'Look of the dashboard navbar. Default (Green) or light (gray)'
      }
    },
    computed: {
      routeName() {
        const { name } = this.$route;
        return this.capitalizeFirstLetter(name);
      },
      userAvatar () {
        return this.$store.state.userInfo ? this.$store.state.userInfo.userAvatar : ''
      },
      userName () {
        return this.$store.state.userInfo ? this.$store.state.userInfo.username : ''
      }
    },
    data() {
      return {
        activeNotifications: false,
        showMenu: false,
        searchModalVisible: false,
        searchQuery: ''
      };
    },
    methods: {

      capitalizeFirstLetter(string) {
        return string.charAt(0).toUpperCase() + string.slice(1);
      },
      toggleNotificationDropDown() {
        this.activeNotifications = !this.activeNotifications;
      },
      closeDropDown() {
        this.activeNotifications = false;
      },
      toggleSidebar() {
        this.$sidebar.displaySidebar(!this.$sidebar.showSidebar);
      },
      hideSidebar() {
        this.$sidebar.displaySidebar(false);
      },
      searchLesson() {
        window.location.href = `https://chinesepod.com/dashboard?search=${this.searchQuery}`
      },
      logout() {
        this.$store.commit('RESET_USER_INFO');
        window.location.href = '/logout'
      }
    }
  };
</script>
