<template>
  <div class="row">
    <div class="col-xl-12">
      <div class="row">
        <div class="col-sm-6">
          <h2 class="display-3">Jump Back In</h2>
          <lesson-card v-if="lessonData[0]" :lesson="lessonData[0]" :courseId="courseId"/>
          <div v-if="title" class="">
              <stats-card class="bg-gradient-blue">
                <!-- Card body -->
                <div class="row">
                  <div class="col">
                    <h5 class="card-title text-uppercase text-muted mb-0 text-white">Main Course <base-button v-if="courseId" size="sm" type="danger" class="float-right" @click.prevent="dropCourse"><i class="fas fa-times"></i>Remove</base-button></h5>
                    <span class="h2 font-weight-bold mb-0 text-white">{{ title }}</span>
                  </div>
                </div>
              </stats-card>
            </div>
        </div>
        <div v-if="lessonData[1] && access !== 'free'"  class="col-sm-6">
          <h2 class="display-3">Next Up...</h2>
          <div class="row">
            <lesson-card v-if="lessonData[1]" :lesson="lessonData[1]" class="col-sm-6" :courseId="courseId"/>
            <lesson-card v-if="lessonData[2]" :lesson="lessonData[2]" class="col-sm-6" :courseId="courseId"/>
          </div>
          <div class="row">
            <lesson-card v-if="lessonData[3]" :lesson="lessonData[3]" class="col-sm-6" :courseId="courseId"/>
            <lesson-card v-if="lessonData[4]" :lesson="lessonData[4]" class="col-sm-6" :courseId="courseId"/>
          </div>
        </div>
        <div v-if="access === 'free'" class="col-sm-6">
          <upgrade />
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  import LessonCard from '@/components/Dashboard/LessonCard'
  import Upgrade from "../Lesson/Upgrade";

  export default {
    name: "mainArea",
    props: {
      lessonData: {
        type: Array,
        description: "Lesson data to show"
      },
      title: {
        type: String
      },
      courseId: {
        type: Number,
        description: 'Course ID'
      }
    },
    components: {
      Upgrade,
      LessonCard
    },
    computed: {
      access() {
        return this.$store.state.userInfo ? this.$store.state.userInfo.access : 'free'
      },
      premium () {
        return ['premium', 'admin'].includes(this.access)
      }
    },
    methods: {
      dropCourse: async function () {
        this.$emit('dropthis');
        this.$notify({type: 'warning', message: `Removed course: ${ this.title }`});
        await this.$http.post(process.env.VUE_APP_ROOT_API + '/api/v1/dashboard/toggle-course', {
          courseId: this.courseId,
          status: false
        });
      }
    }
  }
</script>
