import ExerciseTypes from '@/helpers/ExerciseTypes'

import {
  TOGGLE_PINYIN,
  CHANGE_LANGUAGE,
  SET_MULTIPLE_CHOICE_EXERCISE_DATA,
  SET_SENTENCE_EXERCISE_DATA,
  SET_DICTATION_EXERCISE_DATA,
  SET_MATCHING_EXERCISE_DATA,
  CURRENT_EXERCISE,
  CURRENT_EXERCISE_TYPE,
  ADD_TO_ANSWER_HISTORY,
  RESET_ANSWER_HISTORY
} from '@/mutation-types'

export default {
  namespaced: true,
  state: {
    finished: false,
    lessonId: null,

    multipleChoiceQuestionData: null,
    sentenceQuestionData: null,
    dictationQuestionData: null,
    matchingQuestionData: null,

    multipleChoiceQuestionCount: 0,
    sentenceQuestionCount: 0,
    dictationQuestionCount: 0,
    matchingQuestionCount: 0,

    currentExercise: 0,
    currentExerciseType: ExerciseTypes.getType(0),

    history: []
  },
  mutations: {
    TOGGLE_PINYIN (state) {
      state.pinyin = state.pinyin ? false : true
    },
    TOGGLE_FINISHED (state) {
      state.finished = !state.finished
    },
    SET_LESSON_ID (state, id) {
      state.lessonId = id
    },
    RESET_EXERCISE (state) {
      state.finished = false;
      state.currentExercise = 0;
      state.currentExerciseType = ExerciseTypes.getType(0);
      state.history = [];
    },
    [CHANGE_LANGUAGE](state, language) {
      state.language = language
    },
    [SET_MULTIPLE_CHOICE_EXERCISE_DATA](state, data) {
      // state.multipleChoiceExerciseData = data;
      // state.multipleChoiceQuestionCount = data.questions.length
    },
    [SET_SENTENCE_EXERCISE_DATA](state, data) {
      state.sentenceExerciseData = data;
      state.sentenceQuestionCount = data.questions.length
    },
    [SET_DICTATION_EXERCISE_DATA](state, data) {
      state.dictationExerciseData = data;
      state.dictationQuestionCount = data.questions.length
    },
    [SET_MATCHING_EXERCISE_DATA](state, data) {
      state.matchingExerciseData = data;
      state.matchingQuestionCount = data.questions.length
    },
    SET_MULTIPLE_CHOICE_QUESTION_DATA (state, data) {
      state.multipleChoiceQuestionData = data;
      if (data !== null) {
        state.multipleChoiceQuestionCount = data.length
      }
    },
    SET_SENTENCE_QUESTION_DATA(state, data) {
      state.sentenceQuestionData = data;
      if (data !== null) {
        state.sentenceQuestionCount = data.length
      }
    },
    SET_DICTATION_QUESTION_DATA(state, data) {
      state.dictationQuestionData = data;
      if (data !== null) {
        state.dictationQuestionCount = data.length
      }
    },
    SET_MATCHING_QUESTION_DATA(state, data) {
      state.matchingQuestionData = data;
      if (data !== null) {
        state.matchingQuestionCount = data.length
      }
    },
    [CURRENT_EXERCISE](state, count) {
      state.currentExercise = count
    },
    [CURRENT_EXERCISE_TYPE](state, type) {
      state.currentExerciseType = type
    },
    [ADD_TO_ANSWER_HISTORY](state, answer) {
      state.history.push(answer)
    },
    [RESET_ANSWER_HISTORY](state) {
      state.history = []
    }
  }
}
